body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/***********************
  General app styling
************************/

.mobile-only {
  display: block;
}

.desktop-only {
  display: none;
}

a:hover {
  text-decoration: none;
}

@media screen and (min-width: 600px) {
  .mobile-only {
    display: none;
  }

  .desktop-only {
    display: block;
  }
}

header {
  position: fixed;
  top: 0;
  width: 100vw;
  background: white;
}

.nav-menu a {
  font-size: calc(10px + 2vmin);
  color: darkgreen;
  padding: 1vmin;
  margin: 1vmin;
}

.app-home {
  color: #003400;
  min-height: 100vh;
  background-color: pink;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-shadow: 5px 5px 5px grey;
  background: url(yarn%20start/static/media/HomeBg01.157c58eb.jpg) right top no-repeat;
  background-size: cover;
  background-position-x: center;
  background-position-y: top;
}

.app-home h1 {
  font-size: 15vmin;
}

.app-home h2 {
  font-size: 10vmin;
}

.app-home hr {
  width: 50vw;
  border: 0;
  border-top: 3px solid black;
  box-shadow: 5px 5px 5px grey;
}

.icon-wrapper {
  font-size: 8vmin;
  position: relative;
}

.icon-wrapper .svg-inline--fa {
  margin: 2vmin;
  position: relative;
  animation: tech 2s linear infinite;
}

.icon-wrapper .svg-inline--fa:nth-child(even) {
  animation: tech 2s linear infinite reverse;
}

@keyframes tech {
  0% {
    top: 0px;
  }
  25% {
    top: 0.5vmin;
  }
  50% {
    top: 0px;
  }
  75% {
    top: -0.5vmin;
  }
  100% {
    top: 0px;
  }
}

.about-container {
  background: url(yarn%20start/static/media/AboutBg.a23e7d94.jpg) no-repeat right top;
  background-size: cover;
  min-height: calc(100vh - 3em);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 2em;
  text-align: justify;
  font-size: 2vmin;
}

.about-container h1 {
  margin-top: 2em;
  color: rgb(112, 28, 28);
}

.about-container .coffeeIcon {
  width: 6vmin;
  height: 6vmin;
  color: rgb(112, 28, 28);
}

.about-container .profile {
  width: 30vmin;
  height: 30vmin;
  object-fit: cover;
  margin-top: 3em;
  box-shadow: 5px 5px 5px rgb(93, 121, 93);
}

.about-container p {
  margin-top: 2em;
  font-size: calc(10px + 1.5vmin);
}

@media screen and (min-width: 600px) {
  .about-container p {
    width: 40vw;
  }
}

.contacts-container {
  background: lightgreen;
  min-height: calc(100vh - 3em);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-shadow: 5px 5px 5px grey;
  margin-top: 2em;
  font-size: 2vmin;
  background: url(yarn%20start/static/media/ContactBg01.c9e096dd.jpg) right top no-repeat;
  background-size: cover;
}
.contacts-container h1 {
  font-size: 6vmin;
}

.contacts-container ul {
  list-style-type: none;
  font-size: 4vmin;
}

.contacts-container a {
  color: black;
}

.projects-container {
  background: lightgreen;
  min-height: calc(100vh - 3em);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 2em;
  background: url(yarn%20start/static/media/ProjectBg02.2e5e0f73.jpg) right top repeat;
  background-size: cover;
}

.projects-container h1 {
  font-size: 6vmin;
  text-shadow: 5px 5px 5px grey;
}

.project-cards {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 80vw;
  flex-direction: column;
}

@media screen and (min-width: 600px) {
  .project-cards {
    flex-direction: row;
  }
}

.project-card {
  border: 1px solid green;
  margin-top: 3vh;
  width: 80vw;
}

.project-img {
  height: 15vh;
}

.MuiCollapse-wrapperInner .MuiTypography-paragraph {
  margin-bottom: 0;
}
@media screen and (min-width: 600px) {
  .project-card {
    width: 25vw;
  }
}

